.main-header {
    position: fixed;
    top: 0;
    left: 0;
    height: 1.8rem;
    width: 100%;
    background: whitesmoke;
    display: flex;
    z-index: 1;
}

.logo {
    position: fixed;
    top: 0;
    left: 30px;
    font-weight: 900;
    font-size: large;
    letter-spacing: 9px;
}

.toggle:hover,
.toggle:active {
    background: #ac0e77;
    border-color: #ac0e77;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.26);
    color: whitesmoke;
}

.toggle {
    position: fixed;
    top: 0;
    right: 30px;
    font-size: large;
    font-weight: bolder;
    width: 50px;
    height: 1.8rem;
    text-align: center;
}